@import "./variables";

.container {
    width: 70%;
    @media screen and (max-width: $firstResize) {
        width: 90%;
    }
    @media screen and (max-width: $secondResize) {
        width: 100%;
    }
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}